body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  box-sizing: border-box;
  width: 100%;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: rgb(54, 43, 102);
  color: #fff;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.title{
  color: #fff;
  text-align: center;
}

.btn{
  background-color: rgb(246, 203, 203);
  color: rgb(215, 97, 54);
  font-size: 5rem;
  margin: 0.4rem;
  padding: 0.5rem;
  border: 2px solid salmon;
  border-radius: 0.5rem;
  box-shadow: 0 0.12rem salmon, 0 0.18rem rgba(0, 0, 0, 0.271);
  width: 6rem;
  height: 6rem;
  cursor: pointer;
  display: flex;
  justify-content: center;
  position: relative;
  top: 0;
}

.btn:hover{
  top: 0.2rem;
  color: rgb(154, 87, 173);
  box-shadow: 0 0.4rem salmon, 0 0.8rem rgba(0, 0, 0, 0.12);
}

.btn:active{
  top: 0.4rem;
  box-shadow: 0 0.2rem salmon, 0 0.6rem rgba(0, 0, 0, 0.17);
}

.btn.disabled{
  cursor: default;
}

.btn.disabled:hover{
  top: 0;
  background-color: rgb(246, 203, 203);
  color: rgb(215, 97, 54);
  box-shadow: 0 0.12rem salmon, 0 0.18rem rgba(0, 0, 0, 0.271);
}

.board-line{
  display: flex;
  margin-bottom: 0.2rem;
}

.game{
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
}

.game-info{
  margin: 0.15rem;
  color: #fff;
}

.board > div {
  display: flex;
}
